import React from 'react';
import ReactDOM from 'react-dom'; 
import 'bootstrap/dist/css/bootstrap.css';
import axios from "axios";
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';


// import { createRoot } from 'react-dom/client'

// const axios = require("axios");





class MyFirstComponent extends React.Component{
  state = {
    users: [],
    isLoading: true,
    errors: null,
    val: "",
    count: "US"
  };

  getUsers() {
    // We're using axios instead of Fetch
    axios
      // The API we're requesting data from
      .get("https://newsdata.io/api/1/news?apikey=pub_12092daafe5d7e39f4890255fbb78e901a660&language=en&country="+this.state.count)
      // Once we get a response, we'll map the API endpoints to our props
      .then(response =>
        response.data.results.map(user => ({
          title: `${user.title}`,
          link: `${user.link}`,
          description: `${user.description}`,
          category: `${user.category}`,
          country: `${user.country}`,
        }))
      )
      // Let's make sure to change the loading state to display the data
      .then(users => {
        this.setState({
          users,
          isLoading: false
        });
      })
      // We can still use the `.catch()` method since axios is promise-based
      .catch(error => this.setState({ error, isLoading: false }));
  }

  getUsers1() {
    // We're using axios instead of Fetch
    axios
      // The API we're requesting data from
      .get("https://newsdata.io/api/1/news?apikey=pub_12092daafe5d7e39f4890255fbb78e901a660&language=en&country="+this.state.val)
      // Once we get a response, we'll map the API endpoints to our props
      .then(response =>
        response.data.results.map(user => ({
          name: `${user.name.first} ${user.name.last}`,
          username: `${user.login.username}`,
          email: `${user.email}`,
          image: `${user.picture.thumbnail}`,
          country: `${user.location.country}`,
          state: `${user.location.state}`,
          city: `${user.location.city}`
        }))
      )
      // Let's make sure to change the loading state to display the data
      .then(users => {
        this.setState({
          users,
          isLoading: false
        });
      })
      // We can still use the `.catch()` method since axios is promise-based
      .catch(error => this.setState({ error, isLoading: false }));
  }

  getUsers2() {
    // We're using axios instead of Fetch
    axios
      // The API we're requesting data from
      .get("https://randomuser.me/api/?results=25&nat="+this.state.val +"&format=csv&dl")
      // Once we get a response, we'll map the API endpoints to our props
      .then(response =>
        response.data.results.map(user => ({
          name: `${user.name.first} ${user.name.last}`,
          username: `${user.login.username}`,
          email: `${user.email}`,
          image: `${user.picture.thumbnail}`,
          country: `${user.location.country}`,
          state: `${user.location.state}`,
          city: `${user.location.city}`
        }))
      )
      // Let's make sure to change the loading state to display the data
      .then(users => {
        this.setState({
          users,
          isLoading: false
        });
      })
      // We can still use the `.catch()` method since axios is promise-based
      .catch(error => this.setState({ error, isLoading: false }));
  }

  componentDidMount() {
    this.getUsers();
  }

    render() {
  const { isLoading, users } = this.state;
  return (
    <React.Fragment>
      <h2>Get Latest News</h2>



      <Form>
        
      <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
        <Form.Label>Country</Form.Label>
        <Form.Control type="text" placeholder="Country" value={this.state.count}
          onChange={e => this.setState({ count: e.target.value })} />
      </Form.Group>
        <Button variant="success" type="submit" onClick={() => this.getUsers()}>
        Submit
      </Button>

    </Form>
        <p></p>
       

      <p></p>
      <p></p>
      <div>
        {!isLoading ? (
          users.map(user => {
            const { title, link, description, category, country } = user;
            return (
              <div>
                <p>Title: {title}</p>
                <a href={link}>{link}</a>
                <p></p>
                <p>Description: {description}</p>
                <p>Category: {category}</p>
                <p>Country: {country}</p>
                <hr />
              </div>
            );
          })
        ) : (
          <p>Loading...</p>
        )}
      </div>
    </React.Fragment>
  );
}
}
ReactDOM.render(<MyFirstComponent />, document.getElementById("root"));
